.top-info {
  display: flex;
  justify-content: space-between;
  padding: 10px;
}
.top-info p {
  font-size: 12px;
}
.top-right {
  display: flex;
}
.top-devider {
  width: 1px;
  height: 20px;
  background: #103742;
  margin: 0 20px;
}
.top-social > :first-child {
  margin: 0 10px 0 0;
}

@media screen and (max-width: 750px) {
  .top-info {display: none;}
}